import React, { useEffect, useRef } from 'react';

import { useDispatch, useSelector } from 'react-redux';
import {
  Table as CustomTable,
  Thead,
  Tr,
  Img,
  Text,
  Box,
  // Container,
  Td,
  Th,
  Tbody,
  Input,
  Flex,
  //  IconButton,
  Icon,
  Center,
  HStack,
  Badge,
} from '@chakra-ui/react';

import { useTable, usePagination } from 'react-table';

import automationIcon from '../../../../../styles/Assets/Automation.svg';
import { Link, useNavigate } from 'react-router-dom';
import { GrShieldSecurity } from 'react-icons/gr';
import { FaTrashAlt } from 'react-icons/fa';
import {
  MdEdit,
  MdOutlineAnalytics,
  MdOutlineDisabledByDefault,
  MdOutlineMonitor,
  MdOutlineTouchApp,
} from 'react-icons/md';

import { useTestAnalyticsSlice } from '../../testAnalyticsSlice';
import { AiOutlineCheckSquare, AiOutlineSetting } from 'react-icons/ai';
import { dateTimeConverter } from 'utils/gernricfunction';
import UserPool from 'Authentication/configuration/UserPool';
import { FiTv } from 'react-icons/fi';
import { RiDeviceLine } from 'react-icons/ri';

import { HomePageActions, useHomePageSlice } from 'app/pages/HomePage/slice';
import { selectPageIndexInisialValue } from 'app/pages/HomePage/slice/selectors';
import { getEmailId } from 'Authentication/configuration/GetEmail';
import { BsCheckCircle } from 'react-icons/bs';

