export function PaginatedTable({
  columns,
  data,
  leftIcon,
  rightHoverIcon,
  type,
  getTabIndex,
  editUserClickHandler,
  deleteUserClickHandler,
}) {
  let history = useNavigate();
  const getRowId = id => {
    history(`/testsummary/${id}`);
    //  console.log(id)
  };

  const GetLastAuthUser = () => {
    const last_auth = `CognitoIdentityServiceProvider.${UserPool.getClientId()}.LastAuthUser`;
    const reg = new RegExp(`(?<=${last_auth}=)[^;]*`);
    try {
      return document.cookie.match(reg)[0];
    } catch {
      return null;
    }
  };

  const username = GetLastAuthUser();
  const email = getEmailId();

  const getTabIndexKey = getTabIndex || getTabIndex === 0 ? getTabIndex : '';
  const inputRef = useRef();
  //const { actions } = useManageUserSlice();
  const { homePageActions } = useHomePageSlice();
  const getPageIndexinit = useSelector(selectPageIndexInisialValue);
  const dispatch = useDispatch();
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // Instead of using 'rows', we'll use page,
    // which has only the rows for the active page

    // The rest of these things are super handy, too ;)
    canPreviousPage,
    canNextPage,
    pageOptions,
    // pageCount,
    gotoPage,
    nextPage,
    previousPage,
    //setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      initialState: {
        pageIndex:
          getPageIndexinit.hasOwnProperty('key') &&
          getPageIndexinit.key === `pageIndexKey${type}${getTabIndexKey}`
            ? getPageIndexinit.value
            : 0,
        //getPageIndexForTable(type, getTabIndex),
        //getPageIndexForTable(type)
        // parseInt(localStorage.getItem(`pageIndexKey${type}${getTabIndexKey}`)) === 'NAN' ?  0 :  parseInt(localStorage.getItem(`pageIndexKey${type}${getTabIndexKey}`)),
        pageSize: 20,
      },
      autoResetPage: true,
      // manualPagination: false,
      //autoResetPageIndex : false,
    },
    usePagination,
  );

  // Render the UI for your table
  return (
    <Flex
      flexDirection="column"
      paddingTop="5px"
      width="100%"
      backgroundColor="white"
    >
      <CustomTable
        fontFamily="Montserrat"
        fontWeight="500"
        variant="simple"
        {...getTableProps()}
      >
        <Thead fontWeight="500" color="grey1">
          {headerGroups.map(headerGroup => (
            <Tr {...headerGroup.getHeaderGroupProps()}>
              {/* <Th
                fontFamily="Montserrat"
                minWidth="0px"
                paddingLeft="0px"
                paddingRight="0px"
              ></Th> */}
              {leftIcon && (
                <Th
                  fontFamily="Montserrat"
                  minWidth="0px"
                  paddingLeft="0px"
                  paddingRight="0px"
                ></Th>
              )}
              {headerGroup.headers.map(column => (
                <Th
                  fontFamily="Montserrat"
                  textTransform="capitalize"
                  padding="2px 5px"
                  color="grey1"
                  {...column.getHeaderProps()}
                >
                  {column.render('Header')}
                </Th>
              ))}
              {rightHoverIcon && <Th></Th>}
            </Tr>
          ))}
        </Thead>
        <Tbody fontWeight="500" color="grey13" {...getTableBodyProps()}>
          {page.map((row, i) => {
            prepareRow(row);

            return (
              <Tr
                textDecoration="none"
                cursor={type === 'testAnalyticsTable ' && 'pointer'}
                className="testTr"
                data-scriptappbuild-id={
                  (type === 'deleteappbuilds' || type === 'deletetestsuite') &&
                  row.cells[0].row.original.uuid
                }
                data-manageproject-id={
                  type === 'manangeProjectTable' &&
                  row.cells[0].row.original.uuid
                }
                onClick={() => {
                  if (type === 'testAnalyticsTable ') {
                    getRowId(row.cells[0].row.original.uuid._id);
                  }
                }}
                _hover={rightHoverIcon ? { background: 'grey14' } : ''}
                {...row.getRowProps()}
              >
                {leftIcon && (
                  <Td minWidth="10px" paddingLeft="10px" paddingRight="5px">
                    {type === 'testAnalyticsTable ' ? (
                      row.cells[0].row.original.testType === 'lr-automation' ? (
                        <Icon
                          title="LR Automation"
                          w="22px"
                          h="22px"
                          color="grey1"
                          as={MdOutlineMonitor}
                        ></Icon>
                      ) : row.cells[0].row.original.testType ===
                        'lr-interact' ? (
                        <Icon
                          w="22px"
                          title="LR Interact"
                          h="22px"
                          color="grey1"
                          as={RiDeviceLine}
                        ></Icon>
                      ) : row.cells[0].row.original.uuid.testId !== '' ? (
                        <Icon
                          w="22px"
                          h="22px"
                          title="App Automation"
                          color="grey1"
                          as={AiOutlineSetting}
                        ></Icon>
                      ) : (
                        <Icon
                          color="grey1"
                          w="22px"
                          h="22px"
                          title="Manual"
                          as={MdOutlineTouchApp}
                        ></Icon>
                      )
                    ) : (
                      leftIcon
                    )}
                  </Td>
                )}

                {row.cells.map((cell, index) => {
                  // {console.log(cell.render('Cell').props.data[i].id)}
                  return (
                    <Td
                      color={
                        type === 'testAnalyticsTable '
                          ? row.cells[0].row.original.testStatus === 'Started'
                            ? '#0d5bb5'
                            : row.cells[0].row.original.testStatus === 'Failed'
                            ? '#EF4E90'
                            : ''
                          : ''
                      }
                      // color={
                      //   type === 'testAnalyticsTable ' &&
                      //   row.cells[0].row.original.testStatus === 'Completed'
                      //     ? ' '
                      //     : '#0d5bb5'
                      // }
                      textTransform="none"
                      minWidth={
                        type === 'testAnalyticsTable ' ? '60px' : '70px'
                      }
                      // minWidth={index === 5 ? '100px' : '70px'}
                      padding="5px 4px"
                      {...cell.getCellProps()}
                      minWidth="75px"
                    >
                      {/* get the data of particular cell */}
                      {/* {console.log(row.cells[0].row.original.user_role)} */}

                      {(index === 0 &&
                        type === 'testAnalyticsTable ' &&
                        ((row.cells[0].row.original.testStatus ===
                          'Completed' &&
                          'Success') ||
                          (row.cells[0].row.original.testStatus === 'Aborted' &&
                            'User Aborted'))) ||
                        (index === 1 &&
                          type === 'testAnalyticsTable ' &&
                          dateTimeConverter(
                            row.cells[0].row.original.testStartTime,
                          )) ||
                        (index === 2 &&
                          type === 'journeyTable' &&
                          dateTimeConverter(
                            row.cells[0].row.original.updatedAt,
                          )) ||
                        (index === 2 &&
                          type === 'testAnalyticsTable ' &&
                          row.cells[0].row.original.uuid.sessionId &&
                          row.cells[0].row.original.uuid.sessionId) ||
                        (index == 1 &&
                          type === 'appBuilds' &&
                          new Date(
                            //cell.render('Cell').props.data[i].createdDate,
                            row.cells[0].row.original.createdDate,
                          )
                            .toGMTString()
                            .slice(5, 16)) ||
                        (index === 1 &&
                          (type === 'manageUserTable' ||
                            type === 'manageKeyTable') &&
                          row.cells[0].row.original.user_role ===
                            'SystemAdmin' && (
                            <Text
                              bg="blue1"
                              color="white"
                              align="center"
                              borderRadius="20px"
                              width="120px"
                            >
                              {/* {cell.render('Cell').props.data[i].role} */}
                              {row.cells[0].row.original.user_role}
                            </Text>
                          )) ||
                        (index === 1 &&
                          (type === 'manageUserTable' ||
                            type === 'manageKeyTable') &&
                          row.cells[0].row.original.user_role ===
                            'TenantAdmin' && (
                            <Text
                              bg="blue1"
                              color="white"
                              align="center"
                              borderRadius="20px"
                              width="120px"
                            >
                              {/* {cell.render('Cell').props.data[i].role} */}
                              {row.cells[0].row.original.user_role}
                            </Text>
                          )) ||
                        (index === 3 &&
                          (type === 'manageUserTable' ||
                            type === 'manageKeyTable') &&
                          '*********') ||
                        (index === 4 &&
                          type === 'manageUserTable' &&
                          (row.cells[0].row.original.isMFAEnabled ? (
                            <Icon
                              color="grey1"
                              w="25px"
                              h="25px"
                              as={AiOutlineCheckSquare}
                            ></Icon>
                          ) : (
                            <Icon
                              w="25px"
                              h="25px"
                              color="grey1"
                              as={MdOutlineDisabledByDefault}
                            ></Icon>
                          ))) ||
                        ((type === 'manageUserTable' ||
                          type === 'manageKeyTable') &&
                          index === 2 &&
                          row.cells[0].row.original.status ===
                            'FORCE_CHANGE_PASSWORD' && (
                            <Text>
                              {row.cells[0].row.original.email}{' '}
                              <Badge
                                borderRadius="20px"
                                padding="0px 10px"
                                variant="outline"
                              >
                                {' '}
                                not verified{' '}
                              </Badge>
                            </Text>
                          )) ||
                        cell.render('Cell')}
                    </Td>
                  );
                })}
                {rightHoverIcon && (
                  <Td
                    // minWidth="10px"
                    minWidth="95px"
                    padding="0px 5px"
                    paddingLeft="4px"
                    paddingRight="0px"
                    minWidth={type === 'testAnalyticsTable ' ? '50px' : '95px'}
                  >
                    <Flex
                      sx={{
                        '.testTr:hover &': {
                          display: 'block',
                        },
                      }}
                      id={`box${i}`}
                      display="none"
                      justify="center"
                      alignItems="center"
                    >
                      {type === 'testAnalyticsTable ' && (
                        <Icon
                          as={MdOutlineAnalytics}
                          w="25px"
                          color="grey1"
                          h="25px"
                        ></Icon>
                      )}

                      {type === 'appbuildstable' && rightHoverIcon}
                      {type === 'manageUserTable' && (
                        <HStack align="center" h="30px">
                          <Flex
                            w="30px"
                            data-icon-id={`delete-user-icon-button-${row.cells[0].row.original.user_name}`}
                            h="30px"
                            bg="white"
                            align="center"
                            justify="center"
                            borderRadius="5px"
                            cursor="pointer"
                            onClick={e =>
                              deleteUserClickHandler(
                                row.cells[0].row.original.user_name,
                              )
                            }
                            //   onClick={e => dispatch(actions.deleteUserDetail(row.cells[0].row.original.id)) }
                          >
                            <Icon color="grey1" as={FaTrashAlt}></Icon>
                          </Flex>
                          <Flex
                            //id = {row.cells[0].row.original.id}
                            data-icon-id={`edit-user-icon-button-${row.cells[0].row.original.user_name}`}
                            w="30px"
                            h="30px"
                            bg="white"
                            align="center"
                            justify="center"
                            cursor="pointer"
                            borderRadius="5px"
                            onClick={e =>
                              editUserClickHandler(row.cells[0].row.original)
                            }
                          >
                            <Icon color="grey1" as={MdEdit}></Icon>
                          </Flex>
                        </HStack>
                      )}
                      {type === 'manageKeyTable' && (
                        <HStack align="center" h="30px">
                          <Flex
                            //id = {row.cells[0].row.original.id}
                            data-icon-id={`edit-user-icon-button-${row.cells[0].row.original.user_name}`}
                            w="30px"
                            h="30px"
                            bg="white"
                            align="center"
                            justify="center"
                            cursor="pointer"
                            borderRadius="5px"
                            onClick={e =>
                              editUserClickHandler(row.cells[0].row.original)
                            }
                          >
                            <Icon color="grey" as={GrShieldSecurity}></Icon>
                          </Flex>
                        </HStack>
                      )}
                      {type === 'journeyTable' &&
                        row.cells[0].row.original.createdBy === email && (
                          <HStack align="center" h="30px">
                            <Flex
                              w="30px"
                              data-icon-id={`delete-user-icon-button-${row.cells[0].row.original.id}`}
                              h="30px"
                              bg="white"
                              align="center"
                              justify="center"
                              borderRadius="5px"
                              cursor="pointer"
                              onClick={e =>
                                deleteUserClickHandler(
                                  row.cells[0].row.original.uuid,
                                )
                              }
                              //   onClick={e => dispatch(actions.deleteUserDetail(row.cells[0].row.original.id)) }
                            >
                              <Icon color="grey1" as={FaTrashAlt}></Icon>
                            </Flex>
                            <Flex
                              //id = {row.cells[0].row.original.id}
                              data-icon-id={`edit-user-icon-button-${row.cells[0].row.original.id}`}
                              w="30px"
                              h="30px"
                              bg="white"
                              align="center"
                              justify="center"
                              cursor="pointer"
                              borderRadius="5px"
                              onClick={e =>
                                editUserClickHandler(row.cells[0].row.original)
                              }
                            >
                              <Icon color="grey1" as={MdEdit}></Icon>
                            </Flex>
                          </HStack>
                        )}
                      {type === 'manangeProjectTable' && (
                        <HStack align="center" h="30px">
                          <Flex
                            w="30px"
                            h="30px"
                            data-icon-id={`delete-project-icon-button-${row.cells[0].row.original.uuid}`}
                            bg="white"
                            align="center"
                            justify="center"
                            borderRadius="5px"
                            cursor="pointer"
                            onClick={e =>
                              deleteUserClickHandler(
                                row.cells[0].row.original.uuid,
                              )
                            }
                            // data-delete-project-button-id={
                            //   row.cells[0].row.original.uuid
                            // }
                            //   onClick={e => dispatch(actions.deleteUserDetail(row.cells[0].row.original.id)) }
                          >
                            <Icon color="grey1" as={FaTrashAlt}></Icon>
                          </Flex>
                          <Flex
                            //id = {row.cells[0].row.original.id}
                            data-icon-id={`edit-project-icon-button-${row.cells[0].row.original.uuid}`}
                            w="30px"
                            h="30px"
                            bg="white"
                            align="center"
                            justify="center"
                            cursor="pointer"
                            borderRadius="5px"
                            onClick={e =>
                              editUserClickHandler(row.cells[0].row.original)
                            }
                          >
                            <Icon color="grey1" as={MdEdit}></Icon>
                          </Flex>
                        </HStack>
                      )}
                      {(type === 'deleteappbuilds' ||
                        type === 'deletetestsuite') && (
                        <HStack align="center" h="30px">
                          <Flex
                            w="30px"
                            h="30px"
                            bg="white"
                            align="center"
                            justify="center"
                            borderRadius="5px"
                            cursor="pointer"
                            data-delete-button-id={
                              row.cells[0].row.original.uuid
                            }
                            onClick={e =>
                              deleteUserClickHandler(
                                row.cells[0].row.original.uuid,
                              )
                            }
                            //   onClick={e => dispatch(actions.deleteUserDetail(row.cells[0].row.original.id)) }
                          >
                            <Icon color="grey1" as={FaTrashAlt}></Icon>
                          </Flex>{' '}
                        </HStack>
                      )}
                    </Flex>
                  </Td>
                )}
              </Tr>
            );
          })}
        </Tbody>
      </CustomTable>
      {/* 
        Pagination can be built however you'd like. 
        This is just a very basic UI implementation:
      */}
      <Flex
        justifyContent="flex-end"
        padding="10px 0px"
        marginRight="10px"
        color="grey13"
      >
        <Flex
          fontFamily="Montserrat"
          fontWeight="500"
          className="pagination"
          boxShadow="0px 3px 6px #00000029"
          borderRadius="13"
          marginTop="10px"
          marginRight="5px "
          color="grey2"
          padding="5px 10px"
          display="inline-block"
        >
          <button onClick={() => previousPage()} disabled={!canPreviousPage}>
            <Box as="b" fontWeight="500" borderRadius="50%">
              {' '}
              &lt;{' '}
            </Box>{' '}
            &nbsp;
          </button>{' '}
          <Box as="span" color="black" paddingRight="2px" paddingBottom="3px">
            <Input
              variant="flushed"
              size="18px"
              width="25px"
              _focus={{ color: 'none' }}
              textAlign="right"
              fontSize="14px"
              borderBottom="1.5px solid"
              borderBottomColor="grey2"
              ref={inputRef}
              type="number"
              value={pageIndex + 1}
              onFocus={e => e.target.select()}
              onKeyUp={e => {
                if (e.key === 'Backspace') {
                  e.target.select();
                }
              }}
              onChange={e => {
                const page = e.target.value ? Number(e.target.value) - 1 : 0;
                gotoPage(page);
              }}
            />
          </Box>{' '}
          <Box textTransform="lowercase" as="span" color="black">
            <span>of {pageOptions.length}</span>{' '}
          </Box>
          <button onClick={() => nextPage()} disabled={!canNextPage}>
            &nbsp;
            <Box as="b" fontWeight="500" borderRadius="50%">
              {' '}
              {'  >'}
            </Box>
          </button>{' '}
        </Flex>
      </Flex>
    </Flex>
  );
}
