import { Flex, Icon, useDisclosure } from '@chakra-ui/react'
import LoadingComponent from 'app/components/LoadingComponent';
import NoContent from 'app/components/NoContent';
import { PaginatedTable } from 'app/pages/TestAnalytics/components/Table';
import React, { useState } from 'react'
import { IoMdContact } from 'react-icons/io';
import { useSelector } from 'react-redux';
import { selectManageAccessKey } from '../slice/selector';
import PopupComponent from 'app/components/PopupComponent';
import EditSecret from './EditSecret';

const columns = [
    {
      Header: 'Name',
      accessor: 'username',
    },
  
    {
      Header: 'Role',
      accessor: 'userRole',
    },
  
    {
      Header: 'Email',
      accessor: 'email',
    },
    {
      Header: 'AccessKey',
      accessor: 'accessKey',
    },
  ];
function ManageAccessTable() {
  const { loading, manageAccessKeyData, manageAccessKeyError } = useSelector(
    selectManageAccessKey,
  );
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [userInfo, setUserInfo] = useState({});
  const editUserClickHandler = (userDetail ) => {
    setUserInfo(userDetail);
    onOpen();
  };
 
  return (
    <Flex width="100%"  padding="10px">
    {loading ? (
      <LoadingComponent></LoadingComponent>
    ) : (
      <>
        {manageAccessKeyError === 1 ? (
          <NoContent height="60vh" message="Something Went Wrong"></NoContent>
        ) : (
          <>
            {manageAccessKeyData.length !== 0  ? (
              <PaginatedTable
                leftIcon={
                  <Icon
                    w="22px"
                    h="22px"
                    color="grey1"
                    as={IoMdContact}
                  ></Icon>
                }
                type="manageKeyTable"
                editUserClickHandler={editUserClickHandler}
                rightHoverIcon={true}
                data={manageAccessKeyData}
                columns={columns}
              ></PaginatedTable>
            ) : (
              <NoContent height="60vh" message="NO Data Found"></NoContent>
            )}
          </>
        )}
      </>
    )}
      <PopupComponent
        title="Edit Secret"
        buttonHandler1={onClose}
        width="800px"
        footer={true}
        isOpen={isOpen}
        onClose={onClose}
      >
        <EditSecret onClose={onClose} userData={userInfo}></EditSecret>
      </PopupComponent>
    </Flex>
  )
}

export default ManageAccessTable