// @ts-nocheck
import GetIdToken from 'Authentication/configuration/GetIdToken';
import { call, put, takeLatest, delay, takeEvery } from 'redux-saga/effects';
import { request } from 'utils/request';
import { manageAccessKeyActions as actions } from './';
import { ManageAccessKeyErrorType } from './types';
import { requestNew } from 'utils/requestWithError';
import { REACT_APP_APP_TESTING_BASE_URL } from 'utils/enviroment';
const BASE_URL = REACT_APP_APP_TESTING_BASE_URL;


function* getManageAccessKeyData(param) {
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/authorizer/listkey`;

  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    const manageAccessKeyData = yield call(requestNew, requestURL, config);

    if (
      manageAccessKeyData.hasOwnProperty('body') &&
      manageAccessKeyData.body.hasOwnProperty('result')
    ) {
     
      yield put(
        actions.manageAccessKeyDataLoaded(manageAccessKeyData.body.result),
      );
    } else {
      yield put(
        actions.manageAccessKeyError(ManageAccessKeyErrorType.RESPONSE_ERROR),
      );
    }
  } catch (e) {
    //console.log(e)
    yield put(
      actions.manageAccessKeyError(ManageAccessKeyErrorType.RESPONSE_ERROR),
    );
  }
}

function* deleteManageAccessKeyData(deleteId) {
  console.log(deleteId);
  const idToken = GetIdToken();
  const config = {
    method: 'DELETE',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },

    redirect: 'follow',
  };

  const requestURL = `${BASE_URL}/authorizer/deletekey?accesskey=${deleteId.payload}`;
  try {
    const manageKeyDeleteResponse = yield call(requestNew, requestURL, config);
    //console.log(manageKeyDeleteResponse);
    if (manageKeyDeleteResponse.hasOwnProperty('body')) {
      yield put(actions.deleteManageKeyResponse(manageKeyDeleteResponse));
    } else {
      yield put(
        actions.deleteManageAccessKeyError(
          ManageAccessKeyErrorType.RESPONSE_ERROR,
        ),
      );
    }
  } catch (err) {
    yield put(
      actions.deleteManageAccessKeyError(
        ManageAccessKeyErrorType.RESPONSE_ERROR,
      ),
    );
  }
}
function* createManageAccessKeyData() {
  const idToken = GetIdToken();
  const config = {
    method: 'POST',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },

    redirect: 'follow',
  };

  const requestURL = `${BASE_URL}/authorizer/generate`;
  try {
    const manageKeyCreateResponse = yield call(requestNew, requestURL, config);
 
    if (manageKeyCreateResponse.hasOwnProperty('body')) {
      yield put(actions.createManageKeyResponse(manageKeyCreateResponse));
    } else {
      yield put(
        actions.createManageAccessKeyError(
          ManageAccessKeyErrorType.RESPONSE_ERROR,
        ),
      );
    }
  } catch (err) {
    yield put(
      actions.createManageAccessKeyError(
        ManageAccessKeyErrorType.RESPONSE_ERROR,
      ),
    );
  }
}

export function* manageAccessKeySaga() {
  yield takeLatest(actions.loadManageAccessKeys.type, getManageAccessKeyData);
  yield takeLatest(actions.loadDeleteKey.type, deleteManageAccessKeyData);
  yield takeLatest(actions.loadCreateKey.type, createManageAccessKeyData);
}
