import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { manageAccessKeySaga } from './saga';
import { manageAccessKeyState } from './types';

export const initialState: manageAccessKeyState = {
  loading: false,
  manageAccessKeyData: [],
  manageAccessKeyError: null,
  loadingKeyDelete: false,
  deleteKeyError: null,
  deleteKeyResponse: {},
  loadingKeyCreate: false,
  createKeyError: null,
  createKeyResponse: {},
};

const getPageNumber = (data: number) => {
  return Math.ceil(data / 10);
};

const slice = createSlice({
  name: 'manageAccessKey',
  initialState,
  reducers: {
    loadManageAccessKeys: state => {
      state.loading = true;
    },
    manageAccessKeyDataLoaded: (state, action: PayloadAction<any>) => {
      state.loading = false;

      //console.log(action.payload);
      state.manageAccessKeyData = action.payload;
      state.manageAccessKeyError = null;
    },
    manageAccessKeyError: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.manageAccessKeyError = action.payload;
    },
    loadDeleteKey: state => {
      state.loadingKeyDelete = true;
    },
    deleteManageKeyResponse: (state, action: PayloadAction<any>) => {
      state.loadingKeyDelete = false;
      state.deleteKeyResponse = action.payload;
      state.deleteKeyError = null;
    },
    deleteManageAccessKeyError: (state, action: PayloadAction<any>) => {
      state.loadingKeyDelete = false;
      state.deleteKeyError = action.payload;
    },
    loadCreateKey: state => {
      state.loadingKeyDelete = true;
    },
    createManageKeyResponse: (state, action: PayloadAction<any>) => {
      state.loadingKeyDelete = false;
      state.createKeyResponse = action.payload;
      state.deleteKeyError = null;
    },
    createManageAccessKeyError: (state, action: PayloadAction<any>) => {
      state.loadingKeyDelete = false;
      state.deleteKeyError = action.payload;
    },
  },
});

export const { actions: manageAccessKeyActions } = slice;

export const useManageAccessKeySlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: manageAccessKeySaga });
  return { actions: slice.actions };
};
