import SubLayout from 'app/components/SubLayout'
import React, { useEffect, useState } from 'react'
import Wrapper from './components/Wrapper'
import { HStack, useDisclosure, useToast } from '@chakra-ui/react'
import ManageAccessTable from './Features/ManageAccessTable'
import { useDispatch, useSelector } from 'react-redux'
import { useManageAccessKeySlice } from './slice'
import { selectManageAccessKey } from './slice/selector'
import LoadingWrapper from '../SyntheticMonitoring/components/LoadingWrapper'
import LoaderOverlay from '../SyntheticMonitoring/components/LoaderOverlay'
import useApiResponseHandler from './components/useApiResponseHandler'
import CraeteKeyComponent from './components/CraeteKeyComponent'
import PopupComponent from 'app/components/PopupComponent'
import SecretAcessCopyDownload from './Features/SecretAcessCopyDownload'
import SubContainer from 'app/components/SubContainer'

export function ManageCredentials() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { loading ,loadingKeyDelete,
    deleteKeyError,
    deleteKeyResponse,
    loadingKeyCreate,
    createKeyError,
    createKeyResponse} = useSelector(selectManageAccessKey);
    const [getSecretKey , setSecretKey] = useState({});
    const dispatch = useDispatch()
    const {actions} = useManageAccessKeySlice()
  const toast = useToast();
  useEffect(() => {
    dispatch(actions.loadManageAccessKeys());
  }, []);
  useEffect(() => {
    if (createKeyResponse.hasOwnProperty('statusCode')) {
      if (createKeyResponse.statusCode === 200) {
        toast({
          title: createKeyResponse.body.message,
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        setSecretKey(createKeyResponse.body.response)
        onOpen();
        dispatch(actions.loadManageAccessKeys());
      
      } else {
        toast({
          title: createKeyResponse.body.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.createManageKeyResponse({}));
    }
  }, [createKeyResponse]);

  useEffect(() => {
    if (createKeyError !== null && createKeyError === 1 ) {
      toast({
        title: 'Something went wrong',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
    }
  }, [createKeyError]);

 
  useEffect(() => {
    if (deleteKeyResponse.hasOwnProperty('statusCode')) {
      if (deleteKeyResponse.statusCode === 200) {
        toast({
          title: deleteKeyResponse.body.message,
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        dispatch(actions.loadManageAccessKeys());
      } else {
        toast({
          title: deleteKeyResponse.body.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.deleteManageKeyResponse({}));
    }
  }, [deleteKeyResponse]);

  useEffect(() => {
    if (deleteKeyError !== null && deleteKeyError === 1 ) {
      toast({
        title: 'Something went wrong',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
    }
  }, [deleteKeyError]);
  const createNewKeyHandler = () => {
    dispatch(actions.loadCreateKey())   
}

  return (
    <SubContainer>
    <LoaderOverlay isloading={loadingKeyCreate || loadingKeyDelete} >
 <SubLayout>

<Wrapper>
          <CraeteKeyComponent
            createNewKeyHandler={createNewKeyHandler}
          ></CraeteKeyComponent>
          <ManageAccessTable></ManageAccessTable>
    </Wrapper>
    <PopupComponent
        title="Access Key"
        buttonHandler1={onClose}
        width="800px"
        footer={true}
        isOpen={isOpen}
        onClose={onClose}
      >
  <SecretAcessCopyDownload  getSecretKey={getSecretKey} onClose={onClose} ></SecretAcessCopyDownload>
      </PopupComponent>
    </SubLayout>
    </LoaderOverlay>
   
    </SubContainer>
    )
}

export default ManageCredentials