import { useClipboard } from '@chakra-ui/hooks';
import { Flex, HStack, Spacer, Stack, Text } from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import React, { useEffect } from 'react'
import PopupFooter from '../components/PopupFooter';

function SecretAcessCopyDownload({ getSecretKey , onClose}) {
  const { secretKey, ...keys } = getSecretKey;
  const keyString = JSON.stringify(keys);
  const { onCopy, value, setValue, hasCopied } =
    useClipboard(keyString);

    useEffect(() =>{
    },[getSecretKey])
  
    return (
    
      <Flex mb={2} width="100%" flexDirection='column' >
         {/* <ButtonComp padding="5px 5px" buttonHandler={onCopy}>{hasCopied ? "Copied!" : "Copy"}</ButtonComp> */}
         <Stack direction="row"padding="10px"  width="100%" >
      <Text width="250px" >Access Key </Text>
      <Text width="400px" title={getSecretKey.accessKey} >{getSecretKey.accessKey}</Text>
    </Stack>
         <Stack direction="row"padding="10px"  width="100%" >
      <Text width="250px" whiteSpace="nowrap" >ID Token</Text>
      <Text  width="400px" whiteSpace="nowrap" overflow='hidden' textOverflow='ellipsis' title={getSecretKey.idToken} >{getSecretKey.idToken}</Text>
    </Stack>
   <PopupFooter secondTitle={hasCopied ? "Copied!" : "Copy"} cancelHandler={onClose} updateHandler={onCopy} ></PopupFooter>
      </Flex>
  )
}

export default SecretAcessCopyDownload